import React from "react"
import MobileNav from "./MobileNav";
import SEO from "./elements/SEO";
import {Link} from "gatsby"
import '../i18n/config';
import {useTranslation} from "react-i18next";


const Navigation = ({pageNr}) => {
    require("../style/hero_block.scss");
    require("../style/we_are_block.scss");
    require("../style/kyc_block.scss");
    require("../style/projects.scss");
    const {i18n} = useTranslation();
    const {t} = useTranslation();

    function changeLanguage(e) {
        i18n.changeLanguage(e.target.value);
    }


    return (
        <>
            <SEO/>
            <div className="absolute css--page-number top-12" style={{left: "10%", zIndex: "20"}}>{pageNr}</div>
            <MobileNav/>

            <div className="css--navbar-logo">
                <Link to="/">
                    <img src="/img/logo/logo_text_White-full.svg" style={{width: "100%", opacity: 0.2}} className="pt-5 md:pt-0" alt="Kalmars Logo"/>
                </Link>
            </div>
            <nav className="fixed text-center hidden md:block py-4 px-6 bg-transparent w-full" style={{zIndex: "300"}}>
                <div className="w-4/5 mx-auto flex justify-evenly relative">
                    <Link to="/" className={(pageNr === '01' ? "css--navigation-active" : "") + " text-white font-bold"}>{t('discover')}
                    </Link>
                    <Link to="/about-us" className={(pageNr === '02' ? "css--navigation-active" : "") + " text-white font-bold"}>{t('about_us')}
                    </Link>
                    <Link to="/our-partners" className={(pageNr === '03' ? "css--navigation-active" : "") + " text-white font-bold"}>{t('our_partners')}
                    </Link>
                    <Link to="/portfolio" className={(pageNr === '04' ? "css--navigation-active" : "") + " text-white font-bold"}>{t('portfolio')}
                    </Link>
                    <Link to="/blogs" className={(pageNr === '05' ? "css--navigation-active" : "") + " text-white font-bold"}>{t('blogs')}
                    </Link>
                    <Link to="/career" className={(pageNr === '06' ? "css--navigation-active" : "") + " text-white font-bold"}>{t('career')}
                    </Link>
                    <Link to="/contact-us" className={(pageNr === '07' ? "css--navigation-active" : "") + " text-white font-bold"}>{t('contact_us')}
                    </Link>
                    {/*<div className="flex justify-end">*/}
                    {/*    <button className={`${i18n.language === 'en' ? "hidden" : ""}`} onClick={changeLanguage} value='en'>EN</button>*/}
                    {/*    <button className={`${i18n.language === 'lv' ? "hidden" : ""}`} onClick={changeLanguage} value='lv'>LV</button>*/}
                    {/*</div>*/}
                </div>
            </nav>
        </>
    )
}

export default Navigation;
